const Utils=require("./utils"),G15=1335,G15_MASK=21522,G15_BCH=Utils.getBCHDigit(G15);exports.getEncodedBits=function(errorCorrectionLevel,mask){const data=errorCorrectionLevel.bit<<3|mask;let d=data<<10;for(;Utils.getBCHDigit(d)-G15_BCH>=0;)d^=G15<<Utils.getBCHDigit(d)-G15_BCH;return 21522^(data<<10|d)};